@import url('https://fonts.googleapis.com/css2?family=Abel&family=Balsamiq+Sans:ital,wght@0,400;0,700;1,400;1,700&family=Just+Another+Hand&display=swap');
/* 
font-family: 'Abel', sans-serif;
font-family: 'Balsamiq Sans', sans-serif;
font-family: 'Just Another Hand', cursive; 
*/

#travel-carbon-calc {
  font-family: 'Abel', sans-serif;
  background-color: #fff;
  margin: 0 !important;
  padding: 0 !important;
  height: 100vh; /* For 100% screen height */
  width:  100vw; /* For 100% screen width */
  font-size: 18px;
}

#carbon-logo {
  height: 190px;
  width: auto;
}

/* Back Next buttons */
.back-next-buttons {
  text-align: right;
  padding-bottom: 40px;
  padding-top: 80px;
}

#next_btn {
  background-color: #226666;
  border: 1px solid #226666;
  color: #C3CFA2;
  font-size: 14px;
  font-weight: 600;
  font-family: 'Balsamiq Sans', sans-serif;
  padding: 15px 100px;
  border-radius: 20px;
  transition-duration: 0.3s;
  border-radius: 20px;
  margin-left: 15px;
}
#next_btn:hover {
  background: #A0AF75;
  border: 1px solid #A0AF75;
  color: #ffffff!important;
}
#bck_btn {
  background-color: #EFF1EF;
  border: 1px solid #EFF1EF;
  color: #0C4B4B;
  font-size: 14px;
  font-weight: 600;
  font-family: 'Balsamiq Sans', sans-serif;
  padding: 15px 50px;
  border-radius: 20px;
  transition-duration: 0.3s;
  border-radius: 20px;
}
#bck_btn:hover {
  opacity: 0.6;
}
