.carbon-header {
  display: flex;
  text-align: center;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-bottom: 20px;
}

#carbon-footer {
  background: #142121;
  padding-top: 20px;
  padding-bottom: 20px;
  position:absolute;
  bottom:0;
  width: 100%;
  text-align: center;
}

#carbon-footer p,
#carbon-footer p a {
  color: #a0af75 !important;
  text-decoration: none;
}
