#mobility .mobility-row {
    display: flex;
    text-align: center;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.input-column {
    display: flex;
    text-align: center;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

#mobility button {
    display: inline-block;
    margin: 10px;
    transition-duration: 0.8s;
}

#mobility h2 {
    text-align: center;
}

/* Services */
#aircraftForm {
    padding-top: 20px;
}

.btn-active {
    background-color: #553847!important;
    zoom: 1.1;
}
