#carbon-starter {
    /* display: flex;
    text-align: center;
    flex-direction: column;
    justify-content: center;
    align-items: center; */
    padding-left: 0;
    padding-right: 0;
}


#carbon-starter-header {
  background-image: url('../../assets/nature2.jpg');
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
  width: 100%;
  height: auto;
  padding-top: 150px;
  padding-bottom: 50px;
  margin-bottom: 100px;
}

#carbon-starter-header h1 {
    color: #C3CFA2;
    font-size: 52px;
    font-family: 'Just Another Hand', cursive;
}

#carbon-starter-header h2 {
    font-size: 24px;
    color: #C3CFA2;
    font-family: 'Abel', sans-serif;
    margin-bottom: -20px;
}

#starter-header {
    padding-top: 40px;
    padding-bottom: 40px;
    /* background-color: #0C4B4B; */
    background: rgba(12, 75, 75, 0.8);
    border-radius: 20px;
}

#arrow_icon {
  height:   170px;
  width:    auto;
  position: absolute;
  margin-left: -80px;
  margin-top: -22px;
}

.icons-done {
  height: 70px;
  width: auto;
  margin-right: -12px;
}

#carbon-starter input {
    background-color: #C5D9BF;
    border-color: #C5D9BF;
    color: #0C4949;
    font-size: 16px;
    font-family: 'Abel', sans-serif;
    border-radius: 20px;
    transition-duration: 0.3s;
    padding: 20px;
}

#travel-carbon-calc button.btn-trvl {
    font-size: 16px;
    font-weight: 600;
    font-family: 'Balsamiq Sans', sans-serif;
    color: white;
    padding: 20px;
    border-radius: 20px;
    background-color: #0C4B4B;
    border: 1px solid #0C4B4B;
    transition-duration: 0.3s;
    width: 100%;
  }
  #travel-carbon-calc button.btn-trvl:hover {
    background: #A0AF75;
    border: 1px solid #A0AF75;
    color: #ffffff!important;
  }

  #helper-text-1 {
    text-align: left;
    font-size: 14;
    color: #0C4B4B;
    font-family: 'Balsamiq Sans', sans-serif;
    padding-top: 30px;
    padding-bottom: 10px;
  }

  #helper-text-2 {
    text-align: left;
    font-size: 12;
    color: black;
    font-family: 'Abel', sans-serif;
    padding-bottom: 30px;
  }

  #helper-text-2 {
    margin-bottom: 100px;
  }

/* MEDIA QUERY */
/* LAPTOP */
@media (min-width: 1400px) {}

/* small LAPTOP */
@media (max-width: 1024px){}

/* TABLET */
@media (max-width: 768px){
  #starter-header {
    border-radius: 50px;
  }
  #carbon-starter-header h1 {
    font-size: 45px;
  }
  #carbon-starter-header h2 {
    font-size: 22px;
    margin-bottom: 0px;
  } 
  .icons-done {
    height: 55px;
  }
  #arrow_icon {
    margin-left: -77px;
    margin-top: -16px;
    height: 150px;
  }
  #carbon-starter input,
  #travel-carbon-calc button.btn-trvl  {
    border-radius: 35px;
  }
}

/* MOBILE */
@media (max-width: 475px) {
  #carbon-starter-header {
    padding-top: 100px;
    padding-bottom: 30px;
    margin-bottom: 58px;
  }
  #starter-header {
    padding-top: 18px;
    padding-bottom: 20px;
    border-radius: 72px;
    padding-left: 10px;
    padding-right: 10px;
  }
  #carbon-starter-header h1 {
    font-size: 40px;
  }
  #carbon-starter-header h2 {
    font-size: 18px;
    margin-bottom: 0px;
  } 
  .icons-done {
    height: 45px;
  }
  #arrow_icon {
    margin-left: -47px;
    margin-top: -16px;
    height: 97px;
  }
  #carbon-starter input,
  #travel-carbon-calc button.btn-trvl  {
    border-radius: 35px;
  }
  #helper-text-1,
  #helper-text-2 {
    text-align: center;
  }
  #helper-text-2 {
    margin-top: -11px;
  }
}

@media (max-width: 375px) { }

@media (max-width: 320px) { }