#carbon-services .btn-toolbar {
    justify-content: space-between;
}

#carbon-services h1 {
    font-size: 26px;
    font-weight: 400;
    font-family: 'Balsamiq Sans', sans-serif;
    color: #0C4949;
    padding-top: 150px;
    padding-bottom: 40px;
    text-align: left;
}

#carbon-services h2 {
    padding-top: 80px;
    text-align: left;
}

.btn-srvs {
    font-size: 14px;
    font-weight: 400;
    font-family: 'Balsamiq Sans', sans-serif;
    color: #226666;
    padding: 30px 20px;
    border-radius: 0;
    opacity: 0.7;
    background-color: #C5D9BF;
    border: 1px solid #C5D9BF;
    transition-duration: 0.3s;

}
.btn-srvs.active {
    background-color: #226666;
    border: 1px solid #226666;
    color: #C3CFA2;
    opacity: 1;
}

.trvl-ftprnt {
    background-color: #6f859e;
    border: 1px solid #6f859e;
    color: #0C4949;
    border-radius: 15px;
}
.trvl-compensation {
    border-radius: 15px;
}

#first_group {
    display: flex;
}

#first_group #mobility_btn {
    border-top-left-radius: 15px;
    border-bottom-left-radius: 15px;
}
#first_group #lifestyle_btn {
    border-top-right-radius: 15px;
    border-bottom-right-radius: 15px
}

.btn-arrow-srvcs {
    margin-left:  -10px;
    margin-right: -10px;
}

#add_new_btn {
    font-size: 26px;
    color: #226666;
    font-weight: 800;
    font-family: 'Balsamiq Sans', sans-serif;
    background-color: transparent;
    border: 0px solid red;
    float: left;
    margin-top: 50px;
}

#arrowTop {
    position: absolute;
    height: 70px;
    margin-left: -86px;
    margin-top: -21px;
}

/* Icons */
.mobility-icons {
    background-color: #EFF1EF;
    border-color: #EFF1EF;
    border-radius: 60px;
    transition-duration: 0.3s;
    padding: 43px 10px;
}
    .mobility-icons:hover {
        background-color: #226666;
        border-color: #226666;
    }

.mobility-icons img {
    height: 90px;
    width: auto;
}
.mobility-icons img:hover {

}

.mobility-icons-text {
    color: black;
    font-size: 20px;
    text-decoration: none;
    font-family: 'Abel', sans-serif;
    transition-duration: 0.3s;
    display: inline-block;
    padding-top: 10px;
}

